import React from "react"
import { StaticQuery, Link, graphql } from "gatsby"
import { TwitterTimelineEmbed } from "react-twitter-embed"
import Image from "gatsby-image"

export default function Sidebar() {
  return (
    <StaticQuery
      query={sidebarQuery}
      render={data => {
        const { author, social, title } = data.site.siteMetadata

        return (
          <div className="d-flex flex-column flex-grow-1 align-items-center justify-content-center">
            <h3 className="text-monospace text-center">
              <Link
                className="text-reset text-decoration-none"
                style={{ "word-spacing": "100vw" }}
                to={`/`}
              >
                {title}
              </Link>
            </h3>
            <Image
              className="img-fluid img-thumbnail rounded-circle mx-auto"
              fixed={data.avatar.childImageSharp.fixed}
              alt={author}
            />
            <p className="text-center">
              Written by <strong>{author}</strong>.
              <br/>
              <a
                className="text-reset"
                href={`https://twitter.com/${social.twitter}`}
              >
                You should follow him on Twitter
              </a>
            </p>           
            <TwitterTimelineEmbed
              sourceType={social.twitter}
              screenName={social.twitter}
              options={{ height: 400 }}
            />
          </div>
        )
      }}
    />
  )
}

const sidebarQuery = graphql`
  query {
    avatar: file(absolutePath: { regex: "/profile-pic.png/" }) {
      childImageSharp {
        fixed(width: 50, height: 50) {
          ...GatsbyImageSharpFixed
        }
      }
    }
    site {
      siteMetadata {
        title
        author
        social {
          twitter
        }
      }
    }
  }
`
