import React from "react"
import { Link } from "gatsby"

export default function BlogCard(props) {
  
  const node = props.node
  const title = node.frontmatter.title || node.fields.slug

  return (
    <div class="card bg-light mb-3">
      <div class="card-body">        
        <h5 class="card-title">
          <Link class="link text-monospace text-decoration-none" to={node.fields.slug}>{title}</Link>
        </h5>
        <h6 class="card-subtitle mb-2 text-muted">{node.frontmatter.date}</h6>
        <p class="card-text"
          dangerouslySetInnerHTML={{
            __html: node.frontmatter.description || node.excerpt,
          }}
        />
        <div class="d-flex justify-content-end">
          <Link class="link text-decoration-none text-uppercase" to={node.fields.slug}>read more</Link>
        </div>
      </div>
    </div>
  )
}
